<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">校园安全</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="8">
                <techo-card resizeEvent="leftCard" :maxCard="true">
                    <div slot="header">办学类型</div>
                    <div id="LeftPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="MiddleCard" :maxCard="true">
                    <div slot="header">学生人数</div>
                    <div id="MiddleBar" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="rightCard" :maxCard="true">
                    <div slot="header">安全保卫人数</div>
                    <div id="RightBar" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/campus_security_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 校园安全分布
      topBarOptions: {},
      topBarChart: null,
      // 办学类型比例占比
      leftPieOptions: {},
      leftPieChart: null,
      // 学生人数分布
      middleBarOptions: {},
      middleBarChart: null,
      // 安全保卫人数分布
      rightBarOptions: {},
      rightBarChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 校园安全分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 办学类型比例占比
    this.leftPieChart = this.$echarts.init(this.$el.querySelector("#LeftPie"));
    // 学生人数分布
    this.middleBarChart = this.$echarts.init(
      this.$el.querySelector("#MiddleBar")
    );
    // 安全保卫人数分布
    this.rightBarChart = this.$echarts.init(
      this.$el.querySelector("#RightBar")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getSchoolData(val.data.rawdata.code);
        this.getTypePieData(val.data.rawdata.code);
        this.getStudentsBarData(val.data.rawdata.code);
        this.getGuardsBarData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("leftCard", () => {
      that.leftPieChart.resize();
    });
    this.$bus.$on("MiddleCard", () => {
      that.middleBarChart.resize();
    });
    this.$bus.$on("rightCard", () => {
      that.rightBarChart.resize();
    });
  },
  created() {
    this.getSchoolData();
    this.getTypePieData();
    this.getStudentsBarData();
    this.getGuardsBarData();
  },
  methods: {
    // 校园安全
    getSchoolData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "PeaceSchoolInfo"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);

      service.requestByEntityNames(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "数量";
        this.topBarOptions.legend.data = ["数量"];
        this.topBarOptions.series.push({
          name: "数量",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.topBarOptions.xAxis.data.push(value.name);
          this.topBarOptions.series[0].data.push({
            rawdata: value,
            value: value.count
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 办学类型
    getTypePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewPeaceSchoolInfo",
        fieldName: "type"
      };
      let that = this;
      this.leftPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.leftPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.leftPieOptions.title.text = "办学类型";
        that.leftPieOptions.series[0].name = "校园安全";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.leftPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.leftPieOptions.series[0].radius = "45%";
        that.leftPieOptions.series[0].center = ["70%", "60%"];
        that.leftPieChart.setOption(that.leftPieOptions);
        that.leftPieChart.resize();
      });
    },
    // 学生人数
    getStudentsBarData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "PeaceSchoolInfo",
        fieldName: "studentsCount"
      };
      let that = this;
      this.middleBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndFieldSum(param, data => {
        this.middleBarOptions.yAxis.name = "人数";
        this.middleBarOptions.legend.data = ["人数"];
        this.middleBarOptions.series.push({
          name: "人数",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.middleBarOptions.xAxis.data.push(value.name);
          this.middleBarOptions.series[0].data.push({
            rawdata: value,
            value: value.sum
          });
        });
        this.middleBarChart.setOption(that.middleBarOptions);
        this.middleBarChart.resize();
      });
    },
    // 安全保卫人数
    getGuardsBarData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "PeaceSchoolInfo",
        fieldName: "guardsCount"
      };
      let that = this;
      this.rightBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndFieldSum(param, data => {
        this.rightBarOptions.yAxis.name = "人数";
        this.rightBarOptions.legend.data = ["人数"];
        this.rightBarOptions.series.push({
          name: "人数",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.rightBarOptions.xAxis.data.push(value.name);
          this.rightBarOptions.series[0].data.push({
            rawdata: value,
            value: value.sum
          });
        });
        this.rightBarChart.setOption(that.rightBarOptions);
        this.rightBarChart.resize();
      });
    },
    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getSchoolData(districtCode);
      this.getTypePieData(districtCode);
      this.getStudentsBarData(districtCode);
      this.getGuardsBarData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 23rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 24.2rem);
    width: 100%;
    .el-col-8 {
      height: 100%;
    }
  }
}
</style>